@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
  
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root{
    --primary-color: #fd5056;
    --primary-color-dark: #f7343a;
    --secondary-color: #f3f4f5;
    --secondary-color-dark: #11134f;
    --background-color: #1e1e1e;
    --background-color-dark: #131313;
    --primary-text-color: #f3f4f5;
    --secondary-text-color: #fd5056;
}
.App{
  color: var(--primary-text-color);
  background-color: var(--background-color);
  width: 100%;
  min-height: 100vh;
}
.inline{
  display: inline;
}
.inline-block{
  display: inline-block;
}
.content-section{
  display: grid;
  grid-template-columns: 250px auto;
  min-height: -webkit-max-content;
  min-height: max-content;
}
.footer{
    display: none;
    position: fixed;
    bottom: 0px;
    width: 100%;
    height: 60px;
    background-color: var(--background-color);
}
.footer-items{
    display: flex;
    height: 100%;
    justify-content: space-around;
    align-items: center;
}
.footer-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}
.footer-item-icon{
    font-size: 1.8rem;
}
.footer-item-active{
    color: var(--primary-color);
    font-weight: 600;
}
.footer-item-text{
    margin-top: 3px;
}
@media only screen and (max-width: 800px){
    .footer{
        display: block;
    }
}
.navbar{
    font-size: 1.5rem;
}
.logo{
    margin: 0 2rem 0rem 1rem;
}
.logo-sub-text{
    font-size: inherit;
    color: var(--secondary-text-color);
}
.searchbar-container{
    flex: 2 1;
}
.btn-container-navbar{
    margin: 0 1rem;
}
/* Due to specificity issues i have to write this */
.input-box .navbar-search{
    width: 100%;
}

@media only screen and (max-width: 600px){
    .logo{
        margin: 0 1rem;
    }
}
.button-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 2px;
    font-size: 1.3rem;
}
.button-primary{
    background-color: var(--primary-color);
    color: var(--primary-text-color);
}
.button-primary:hover{
    background-color: var(--primary-color-dark);
    border: 2px solid var(--primary-color-dark);
}
.button-outline-secondary{
    background-color: transparent;
    border: 2px solid var(--primary-color);
}
.button-outline-secondary:hover{
    background-color: var(--primary-color);
}
.sidebar{
    grid-column-start: 1;
    grid-column-end: 2;
    background-color: var(--background-color);
    width: 250px;
}
.sidebar-items{
    margin-top: 1.5rem;
}
.sidebar-item{
    display: flex;
    align-items: center;
    width: 100%;
    padding: 1rem 0;
    margin: 0 7px;
}
.sidebar-item-active{
    border-bottom: 2px solid var(--primary-color);
    border-left: 2px solid var(--primary-color);
    border-top: 2px solid var(--primary-color);
    border-bottom-left-radius: 0.5rem;
    border-top-left-radius: 0.5rem;
}
.sidebar-icon{
    margin: 0 0.5rem 0 1rem;
}
.hero-section{
    width: 100%;
    height: 90vh;
    overflow: hidden;
}
.hero-image-container{
    width: 100%;
    height: 100%;
}
.hero-image{
    -webkit-filter: brightness(30%);
            filter: brightness(30%);
}
.hero-heading{
    font-size: 3rem;
    font-weight: 600;
}
.hero-button{
    margin-top: 1rem;
}
.hero-heading-sub-text{
    font-size: inherit;
    color: var(--primary-color);
}
.hero-text-overlay-container{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
@media only screen and (max-width: 1250px){
    .hero-text-overlay-container{
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-30%, -50%);
                transform: translate(-30%, -50%);
    }   
}
@media only screen and (max-width: 800px){
    .hero-text-overlay-container{
        /* display: flex;
        flex-direction: column;
        align-items: center;
        width: fit-content; */
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }   
}
.content{
    grid-column-start: 2;
    grid-column-end: 3; 
}
@media only screen and (max-width: 800px){
    .content{
        grid-column-start: 1;
        grid-column-end: 3; 
    } 
}
.videos-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 1rem;
}
.videolisting-video-container{
    width: 20rem;
    margin: 0.5rem;
    cursor: pointer;
    background-color: rgb(49, 47, 47);
}
.videolisting-video{
    width: 100%;
    height: 100%;
}
.thumbnail-text-container{
    display: grid;
    grid-template-columns: 50px auto;
    margin: 0.5rem;
}
.thumbnail-container{
    grid-column-start: 1;
    grid-column-end: 2;
    width: 2.8rem;
    height: 2.8rem;
}
.text-container{
    position: relative;
    grid-column-start: 2;
    grid-column-end: 3;
    margin: 0 1.5rem 0 0.3rem;
}
.video-title{
    width: 100%;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    white-space: normal;
}
.video-sub-title{
    opacity: 0.8;
    margin-top: 0.3rem;
    font-size: 0.9rem;
}
.video-more-icon{
    position: absolute;
    top: 0.3rem;
    right: -20px;
    font-size: 1.5rem;
    cursor: pointer;
}
.video-modal{
    position: absolute;
    top: 1.9rem;
    right: -20px;
    width: -webkit-max-content;
    width: max-content;
    background-color: var(--background-color);
    z-index: 2;
    
}
.video-modal-item{
    display: flex;
    align-items: center;
    padding: 1rem 1rem;
    cursor: pointer;
    font-size: 1rem;
}
.video-modal-item:hover{
    background-color: var(--background-color-dark);
}
.video-modal-icon{
    margin-right: 10px;
    font-size: 1.2rem;
}
@media only screen and (max-width: 950px){
    .videolisting-video-container{
        width: 15rem;
        margin: 0.5rem;
        cursor: pointer;
        background-color: rgb(49, 47, 47);
    }
}
@media only screen and (max-width: 452px){
    .videolisting-video-container{
        width: 100%;
        margin: 1rem;
        cursor: pointer;
        background-color: rgb(49, 47, 47);
    }
}
